.navigators {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  position: fixed;
  left: 0;
}

.youtube {
  display: flex;
  align-items: center;
  justify-content: center;
}

.navigators__navig {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.logotip__navig {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 10px;
  border-radius: 10px;
  transition: 0.1s ease-in-out;
  cursor: pointer;
}
.logotip__navig:hover {
  background-color: #33333363;
}
.logotip__navig img {
  margin: 10px;
}

@media screen and (min-width: 300px) and (max-width: 500px) {
  .navigators {
    display: none;
  }
  .navbar {
    width: 100%;
  }

  .public__videos {
    width: 100%;
  }
  .videos {
    width: 100%;
    border: 1px solid red;
  }
}
@media screen and (min-width: 500px) and (max-width: 800px) {
  .navigators {
    width: 20%;
  }
 
}
